.titleBar {
display: flex;
flex-direction: row;
align-items: center;
justify-content: center;
background-color: #6082B6;
color: #FFFFF2;
position: fixed;
top: 0;
width: 100%;
height: 10vh;
margin: 0;
}
  
.DaLogo {
height: 10vmin;
pointer-events: none;
margin-right: 4vmin;
}

@media (prefers-reduced-motion: no-preference) {
.DaLogo {
    animation: DaLogo-spin infinite 5s linear;
}
}

@keyframes DaLogo-spin {
from {
    transform: rotate(0deg);
}
to {
    transform: rotate(360deg);
}
}


